import React from 'react';
function Registration() {
    let [firstname, setfirstname] = React.useState('');
    let [lastname, setlastname] = React.useState('');
    let [password, setpassword] = React.useState('');
    let [email, setemail] = React.useState('');
    function handleFirstName(event) {
        setfirstname(event.currentTarget.value);
    }
    function handleLastName(event) {
        setlastname(event.currentTarget.value);
    }
    function handlePassword(event) {
        setpassword(event.currentTarget.value);
    }
    function handleEmail(event) {
        setemail(event.currentTarget.value);
    }
    function handleSignUp(event) {
        event.preventDefault();
        if (email === "" && password === "" && firstname === "" && lastname === "") {
            alert("you should fill all the information ");

        }
        else { alert("Thank you for your registration") }
    }


    return (
        <div>
            <h2> Courses and Workshops</h2>
            <p> To register, please enter the requested information.upon successful registration , you will be sent an email with instractions to verify your registration</p>

            <form>

                <div className="container">
                    <label >First Name </label>
                    <input type="text" placeholder="First Name" value={firstname} onChange={handleFirstName} required />

                    <label>Last Name </label>
                    <input type="text" placeholder="Last Name" value={lastname} onChange={handleLastName} required/>

                    <label> Email</label>
                    <input type="text" placeholder="Enter Email" value={email} onChange={handleEmail} required />
                    
                    <label>Password </label>
                    <input type="password" placeholder="Enter Password"value={password} onChange={handlePassword} required/>

                    <button type="submit" className="registerbtn" onClick={handleSignUp}>sign up</button>

                </div>
            </form>

        </div>

    );
}
export default Registration;