import React from 'react';
import { Link } from 'react-router-dom';
function Cart(props) {
    function removeFromCart(product) {
        let newCart = props.cart.slice(0);
        for (let i = 0; i < newCart.length; i++) {
            let currentCartItem = newCart[i];
            if (product.id === currentCartItem.id) {
                newCart.splice(i, 1);
                break;
            }
        }
        props.setCart(newCart);
    }
    let cartTotal = 0;
    for (let product of props.cart) {
        cartTotal = cartTotal + product.price;
    }
    return (
        <div className="body">
            <h1>your cart</h1>
            <p>
                <Link className="gotocard" to="/Purchase">
                    Go Purchase
                </Link>
            </p>
            <hr />
            <h2> your total is: ${cartTotal}</h2>

            <div className="cart">
                {props.cart.map(function (product) {
                    return (
                        <div className="cart-row">
                            <img src={product.imageUrl} style={{ width: 284, height: 213 }} />

                            <h2>{product.name}</h2>
                            <div>
                            <h3>price: ${product.price}</h3>
                                <button className='bottunred' onClick={() => removeFromCart(product)}> Remove </button>
                            </div>
                        </div>
                    )
                })}

            </div>



        </div>


    )
}
export default Cart;