import React from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';
import Index from './pages/Index';
import Contact from './pages/Contact';
import About from './pages/About';
import Registration from './pages/Registration';
import Purchase from './pages/Purchase';
import cover from './pages/cover.png';
import fish from './pages/fish.png';
import metabolism from './pages/metabolism.jpg';
import Cart from './pages/Cart';




function App() {
  let [cart, setCart] = React.useState([]);
  let products = [
    {
      name: 'Gonadotropin-releasing hormone, kisspeptin, and gonadal steroids directly modulate nucleobindin-2/nesfatin-1 in murine hypothalamic gonadotropin-releasing hormone neurons and gonadotropes',
      imageUrl: cover,
      price: 50,
      link: 'https://academic.oup.com/biolreprod/article/96/3/635/2962085?searchresult=1'
    },
    {
      name: ' The Journal of Fish Biology is a leading international, peer-review journal for scientists engaged in all aspects of fish biology.the journal encompasses all aquatic ecosystems: marine, estuarine and fresh water.',
      imageUrl: fish,
      price: 20
    },
    {
      name: 'Metabolism promotes excellence in research by publishing high-quality original research papers, fast-tracking cutting-edge papers, research brief reports, mini-reviews, and other special articles related to all metabolism.',
      imageUrl: metabolism,
      price: 70
    },
  ];



  return (
    <BrowserRouter basename={'/comit-final-project'}>
      <div >
        <nav>
          <ul>
            <li ><Link to='/'> Home</Link></li>
            <li ><Link to="/Purchase"> Purchase</Link> </li>
            <li ><Link to="/Cart"> Cart</Link> </li>
            <li><Link to="/Contact">Contact</Link></li>
            <li><Link to="/About"> About</Link> </li>
            <li> <Link to="/Registration"> Registration</Link> </li>
          </ul>
        </nav>
        <Route path="/" component={Index} exact={true}>

        </Route>
        <Route path="/Contact" component={Contact} exact={true} />
        <Route path="/About" component={About} exact={true} />
        <Route path="/Registration" component={Registration} exact={true} />
        <Route path="/Purchase" component={Purchase} exact={true}>
          <Purchase
            cart={cart}
            setCart={setCart}
            products={products}
          />
        </Route>


        <Route path="/cart">
          <Cart
            cart={cart}
            setCart={setCart}
          />

        </Route>

      </div>
    </BrowserRouter>

  );
}

export default App;
