import React from 'react';
import {Link} from 'react-router-dom';
function Purchase(props) {
    function addTocart(product){
        let newCart=props.cart.slice(0);
        newCart.push(product);
        props.setCart(newCart);
    }

    return (
        <div className="body">
            <h1>Welcome to the world of Aquatic Animal Papers and courses</h1>
            <p>you have {props.cart.length}  item in your cart</p>
            <Link className="gotocard" to="/cart"> go to your cart</Link>
            <hr/>
            <div className="products">
                {props.products.map(function (product) {
                    return (
                        <div className="product">
                            <img src={product.imageUrl} style={{width:284 , height:213}}/>
                            <h2>{product.name}</h2>
                            <p>${product.price}</p>
                            <button className='bottun' onClick={()=>addTocart(product)}>add to cart</button>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}
export default Purchase;